  @media (min-width: 1280px) {
    .content, footer {
      width: 1280px;
      margin-left: auto;
      margin-right: auto;
      padding-left: 30px;
      padding-right: 30px; } }

.clear-both {
  clear: both; }

footer {
  display: flex;
  margin-top: 47px;
  margin-bottom: 57px; }
  footer a {
    text-decoration: none; }
  footer .with-love {
    margin-top: 0;
    font-size: 19px; }
    @media (max-width: 424px) {
      footer .with-love {
        margin-top: 25px; }
        footer .with-love .from {
          display: none; } }
    footer .with-love .icon-love {
      margin-left: 3px;
      margin-right: 3px; }
      footer .with-love .icon-love:hover {
        animation: heartbeat 1.2s ease-in-out infinite; }

@keyframes heartbeat {
  0% {
    transform: scale(1); }
  14% {
    transform: scale(1.3); }
  28% {
    transform: scale(1); }
  42% {
    transform: scale(1.3); }
  70% {
    transform: scale(1); } }
  footer .fork-and-fix {
    margin-left: auto;
    font-size: 14px;
    align-self: flex-end; }
    @media (max-width: 424px) {
      footer .fork-and-fix {
        display: none; } }

nav {
  display: flex;
  margin-top: 60px;
  margin-left: 80px;
  margin-right: 80px; }
  @media (max-width: 424px) {
    nav {
      margin-top: 25px;
      margin-left: 25px;
      margin-right: 25px; } }
  @media (min-width: 1280px) {
    nav {
      width: 1280px;
      margin-left: auto;
      margin-right: auto;
      padding-left: 30px;
      padding-right: 30px; } }
  nav > a {
    align-self: center;
    color: #747474;
    font-size: 16px;
    line-height: 26px;
    margin-right: 34px;
    text-decoration: none;
